























import {Component, Vue} from 'vue-property-decorator';
import GithubRow from "./GithubRow.vue";

@Component<Github>({
	components: {
		GithubRow,
	},
})
export default class Github extends Vue {
	data: Array<object> = [];

	updateData() {
		fetch("https://github-trending-api.now.sh/repositories?since=daily")
			.then(response => {
				return response.json();
			})
			.then(json => {
				this.data = json;
			});
	}

	created() {
		this.updateData();
	}
}
